

/* Header */
.hero-container:before {
  filter: blur(2px);
}

/* Chat Box */
.chat-history {
    overflow-y: scroll !important;
    overflow-x: auto;
    height: 70vh;
}

.chat-history::-webkit-scrollbar {
    width: 5px;
    border-radius: 2.5px;
  }
   
  .chat-history::-webkit-scrollbar-thumb {
    background: rgb(161, 161, 161); 
  }

  .range-slider__range {
    -webkit-appearance: none;
    width: calc(100% - (73px));
    height: 10px;
    border-radius: 5px;
    background: #d7dcdf;
    outline: none;
    padding: 0;
    margin: 0;
  }
  .range-slider__range::-webkit-slider-thumb {
    appearance: none;
    width: 20px;
    height: 20px;
    border-radius: 50%;
    background: #ff9800;
    cursor: pointer;
    transition: background 0.15s ease-in-out;
  }
  .range-slider__range::-webkit-slider-thumb:hover {
    background: #da3a3a;
  }
  .range-slider__range:active::-webkit-slider-thumb {
    background: #da3a3a;
  }
  .range-slider__range::-moz-range-thumb {
    width: 20px;
    height: 20px;
    border: 0;
    border-radius: 50%;
    background: #ff9800;
    cursor: pointer;
    transition: background 0.15s ease-in-out;
  }
  .range-slider__range::-moz-range-thumb:hover {
    background: #da3a3a;
  }
  .range-slider__range:active::-moz-range-thumb {
    background: #da3a3a;
  }
  .range-slider__range:focus::-webkit-slider-thumb {
    box-shadow: 0 0 0 3px #fff, 0 0 0 6px #da3a3a;
  }
  .range-slider__value {
    display: inline-block;
    position: relative;
    width: 60px;
    color: #fff;
    line-height: 20px;
    text-align: center;
    border-radius: 3px;
    background: #2c3e50;
    padding: 5px 10px;
    margin-left: 8px;
  }
  .range-slider__value:after {
    position: absolute;
    top: 8px;
    left: -7px;
    width: 0;
    height: 0;
    border-top: 7px solid transparent;
    border-right: 7px solid #2c3e50;
    border-bottom: 7px solid transparent;
    content: '';
  }
  ::-moz-range-track {
    background: #d7dcdf;
    border: 0;
  }
  input::-moz-focus-inner, input::-moz-focus-outer {
    border: 0;
  }
  