.dash-container {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    /* padding-bottom: 64px; */
}

.dash-left {
    width: 50%;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
}
.dash-left > h4 {
    text-align: center;
    color: #FA5100;
    font-size: 1.5rem;
    margin-bottom: 5px;
}
.dash-left > h4 > b {
